import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import Particles from "react-particles-js";

const useStyles = makeStyles({
    particlesCanva: {
        position: "fixed",
        opacity: "0.3",
    },
})

const CustomParticles = () => {
    const classes = useStyles();
    
    return (
        <Particles canvasClassName={classes.particlesCanva}
            params={{
                particles:{
                    number: {
                        value: 45,
                        density: {
                            enable: true,
                            value_area: 900
                        }
                    },
                    shape: {
                        type: "circle",
                        strole: {
                            width: 1,
                            color: "tomato"
                        }
                    },
                    size: {
                        value: 8,
                        random: true,
                        animation: {
                            enable: true,
                            speed: 10,
                            size_min: 0.1,
                            sync: true
                        }
                    },
                    opacity: {
                        value: 1,
                        random: true,
                        animation: {
                            enable: true,
                            speed: 1,
                            opacity_min: 0.1,
                            sync: true
                        }
                    }
                }
            }}
        />
    );
}

export default CustomParticles;